.Question {
  margin: 10px auto;
  padding: 25px 10px;
  max-width: 710px;
  border-radius: 8px;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(250, 145, 97, 0.7)), to(rgba(247, 59, 28, 0.69)));
  background-image: -webkit-linear-gradient(top, rgba(250, 145, 97, 0.7), rgba(247, 59, 28, 0.69));
  background-image: -o-linear-gradient(top, rgba(250, 145, 97, 0.7), rgba(247, 59, 28, 0.69));
  background-image: linear-gradient(to bottom, rgba(250, 145, 97, 0.7), rgba(247, 59, 28, 0.69));
  -webkit-box-shadow: 0 2px 12px 6px rgba(167, 196, 220, 0.5);
  box-shadow: 0 2px 12px 6px rgba(167, 196, 220, 0.5); }
  .Question.correct {
    background-image: -webkit-gradient(linear, left top, left bottom, from(#47e4c1), to(#07cddd));
    background-image: -webkit-linear-gradient(top, #47e4c1, #07cddd);
    background-image: -o-linear-gradient(top, #47e4c1, #07cddd);
    background-image: linear-gradient(to bottom, #47e4c1, #07cddd); }
  .Question p.question {
    font-size: 17px;
    font-weight: bold; }



